const languageReducer = (state = { texts: INS['English'], langNow: 'English', languages: Object.keys(INS) }, action) => {
    switch (action.type) {
        case CHANGE_LEGUAGE:

            return { texts: INS[action.payload], langNow: action.payload, languages: Object.keys(INS) }
        default:
            return state
    }


}
export default languageReducer